<template>
    <div>
         <van-nav-bar
            title="账号管理"
            left-text="返回" 
            @click-left="toback"
            :fixed="true"
            />
            <van-cell-group>
                <van-cell title="头像"  class="info-msg" >
                    <div class="clearfix info-img">
                        <img class="auto-img fr" :src="useInfo.userImg" >
                        <van-uploader class="info-change" :after-read="changeImg" />
                    </div>
                </van-cell>
                <van-cell title="用户id"  class="info-msg" >
                    <div>{{useInfo.userId}}</div>
                </van-cell>
                <van-cell title="手机号"  class="info-msg" >
                    <div>{{useInfo.phone}}</div>
                </van-cell>
                <van-cell title="昵称"  class="info-msg" >
                    <van-field @blur="changeName({key: 'nickName', value: useInfo.nickName, url: '/updateNickName'})" v-model="useInfo.nickName" input-align="right" />
                </van-cell>
                <div class="info-msg">
                    <div class="msg-top">简介</div>
                    <textarea @change="changeMsg({key: 'desc', value: useInfo.desc, url: '/updateDesc'})" class="textarea" v-model="useInfo.desc"></textarea>
                </div>
                    
                
            </van-cell-group>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                useInfo:{}
            }
        },
        created(){
            this.getInfo();
        },
        methods:{
            toback(){
                this.$router.go(-1)
            },
            getInfo(){
                let tokenString = localStorage.getItem("login_Tk");

                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });

                this.axios({
                    method: "GET",
                    url: "/findAccountInfo",
                    params: {
                        appkey: this.appkey,
                        tokenString
                    }
                }).then(result => {
                    this.$toast.clear();
                    
                    if (result.data.code == "B001") {
                        this.useInfo = result.data.result[0];
                    }
                    
                }).catch(err => {
                    this.$toast.clear();
                });
            },
            changeImg(file){
                
                let img = file.content.replace(/^data:image\/[A-Za-z]+;base64,/, '');

                let imgType = file.file.type.split('/')[1];

                let tokenString = localStorage.getItem("login_Tk");
                
                this.axios({
                    method: "POST",
                    url: "/updateAvatar",
                    data: {
                        appkey: this.appkey,
                        tokenString,
                        imgType,
                        serverBase64Img:img,
                    }
                })
                .then(result => {
                    this.$toast.clear();
                    
                    
                    if (result.data.code == 'H001') {
                        this.useInfo.userImg = result.data.userImg;
                    } else {
                        this.$toast(result.data.msg);
                    }
                    
                })
                .catch(err => {
                    this.$toast.clear();
                    
                });
            },
            changeName(obj){
                let tokenString = localStorage.getItem("login_Tk");

                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });

                this.axios({
                    method: "POST",
                    url: obj.url,
                    data: {
                        appkey: this.appkey,
                        tokenString,
                        [obj.key]: obj.value
                    }
                })
                .then(result => {
                    this.$toast.clear();
                        
                })
                .catch(err => {
                    this.$toast.clear();
                        
                });
            },
            changeMsg(obj){
                let tokenString = localStorage.getItem("login_Tk");

                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });

                this.axios({
                    method: "POST",
                    url: obj.url,
                    data: {
                        appkey: this.appkey,
                        tokenString,
                        [obj.key]: obj.value
                    }
                })
                .then(result => {
                    this.$toast.clear();
                    
                })
                .catch(err => {
                    this.$toast.clear();
                    
                });
            }
        }
    }
</script>

<style lang="less" scoped>
.info-msg:first-child{
    margin-top: 50px;
}
.info-img{
    position: relative;
    >img{
        width: 40px;
        height: 40px;
    }
    .info-change{
        position: absolute;
        top: 0;
        right: 0;
        opacity: 0;
        width: 40px;
        height: 40px;
    }
}
.textarea{
    width: 340px;
    margin-top: 10px;
    margin-left: 10px;
    height: 100px;
}
</style>